import type { AppProps } from "next/app";
import { Mail, MapPin, Phone, Send } from "lucide-react";
import { useState } from "react";
import { sendContactForm } from "@/lib/api";

export default function Contact() {
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    message: "",
  });
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [submitMessage, setSubmitMessage] = useState("");
  const handleChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsSubmitting(true); // Indicate form submission started
  
    try {
      const response = await sendContactForm(formData); // Await API response
  
      setSubmitMessage(response.message || "Thank you for your message! I will get back to you soon.");
      setFormData({ name: "", email: "", message: "" });
  
    } catch (error) {
      console.error("Form submission error:", error);
      setSubmitMessage("Something went wrong. Please try again.");
    } finally {
      setTimeout(() => {
        setIsSubmitting(false);
        
      }, 1500);
      setTimeout(() => {
        
        setSubmitMessage("");
      }, 5000);
    }
  };
  

 
  return (
    <>
      <section id="contact" className="w-full min-h-screen py-20">
        <div className="container mx-auto px-6">
          <div className="text-center mb-16">
            <h2 className="text-3xl md:text-4xl text-github font-bold mb-4">
              Get In Touch
            </h2>
            <div className="bg-header h-1 w-20 mx-auto" />
            <p className="mt-4 text-subhead max-w-xl mx-auto ">
              Have a question or want to work together? Feel free to reach out!
            </p>
          </div>
          <div className="grid grid-cols-2 gap-3">
            <div className="bg-white p-8 rounded-lg shadow-md">
              <div className="flex flex-col space-y-4">
                <h3 className="text-2xl font-semibold text-gray-800 mb-6">
                  Contact Information
                </h3>
                <div className="flex flex-row ">
                  <div className="bg-indigo-100 p-3 rounded-full mr-4">
                    <Mail className="text-indigo-600" size={20} />
                  </div>
                  <div className="flex flex-col">
                    <h4 className="text-gray-600 font-semibold">Email</h4>
                    <a
                      href="mailto:nigeehettige20@gmail.com"
                      className="text-indigo-600 hover:underline"
                    >
                      nigeehettige20@gmail.com
                    </a>
                  </div>
                </div>
                <div className="flex flex-row">
                  <div className="bg-indigo-100 p-3 rounded-full mr-4">
                    <MapPin className="text-indigo-600" size={20} />
                  </div>
                  <div className="flex flex-col">
                    <h4 className="text-gray-600 font-semibold">Location</h4>
                    <p className="text-gray-800">Colombo,Western Province</p>
                  </div>
                </div>
                <div className="flex flex-row">
                  <div className="bg-indigo-100 p-3 rounded-full mr-4">
                    <Mail className="text-indigo-600" size={20} />
                  </div>
                  <div className="flex flex-col">
                    <h4 className="text-gray-600 font-semibold">Phone</h4>
                    <a
                      href="tel:+11234567890"
                      className="text-indigo-600 hover:underline"
                    >
                      +94 77 8082156
                    </a>
                  </div>
                </div>
              </div>
              <div className="mt-8">
                <h3 className="text-2xl font-semibold text-gray-800 mb-6">
                  Connect with me
                </h3>
                <div className="flex flex-row space-x-4">
                  <a
                    href="https://github.com/NigeeHettige"
                    target="_blank"
                    rel="noopener noreferrer"
                    className="bg-gray-800 hover:bg-gray-700 text-white p-3 rounded-full transition-colors duration-300"
                  >
                    <svg
                      className="w-5 h-5"
                      fill="currentColor"
                      viewBox="0 0 24 24"
                      aria-hidden="true"
                    >
                      <path
                        fillRule="evenodd"
                        d="M12 2C6.477 2 2 6.484 2 12.017c0 4.425 2.865 8.18 6.839 9.504.5.092.682-.217.682-.483 0-.237-.008-.868-.013-1.703-2.782.605-3.369-1.343-3.369-1.343-.454-1.158-1.11-1.466-1.11-1.466-.908-.62.069-.608.069-.608 1.003.07 1.531 1.032 1.531 1.032.892 1.53 2.341 1.088 2.91.832.092-.647.35-1.088.636-1.338-2.22-.253-4.555-1.113-4.555-4.951 0-1.093.39-1.988 1.029-2.688-.103-.253-.446-1.272.098-2.65 0 0 .84-.27 2.75 1.026A9.564 9.564 0 0112 6.844c.85.004 1.705.115 2.504.337 1.909-1.296 2.747-1.027 2.747-1.027.546 1.379.202 2.398.1 2.651.64.7 1.028 1.595 1.028 2.688 0 3.848-2.339 4.695-4.566 4.943.359.309.678.92.678 1.855 0 1.338-.012 2.419-.012 2.747 0 .268.18.58.688.482A10.019 10.019 0 0022 12.017C22 6.484 17.522 2 12 2z"
                        clipRule="evenodd"
                      />
                    </svg>
                  </a>
                  <a
                    href="https://www.linkedin.com/in/nigee-hettige/"
                    target="_blank"
                    rel="noopener noreferrer"
                    className="bg-blue-600 hover:bg-blue-500 text-white p-3 rounded-full transition-colors duration-300"
                  >
                    <svg
                      className="w-5 h-5"
                      fill="currentColor"
                      viewBox="0 0 24 24"
                      aria-hidden="true"
                    >
                      <path d="M20.447 20.452h-3.554v-5.569c0-1.328-.027-3.037-1.852-3.037-1.853 0-2.136 1.445-2.136 2.939v5.667H9.351V9h3.414v1.561h.046c.477-.9 1.637-1.85 3.37-1.85 3.601 0 4.267 2.37 4.267 5.455v6.286zM5.337 7.433c-1.144 0-2.063-.926-2.063-2.065 0-1.138.92-2.063 2.063-2.063 1.14 0 2.064.925 2.064 2.063 0 1.139-.925 2.065-2.064 2.065zm1.782 13.019H3.555V9h3.564v11.452zM22.225 0H1.771C.792 0 0 .774 0 1.729v20.542C0 23.227.792 24 1.771 24h20.451C23.2 24 24 23.227 24 22.271V1.729C24 .774 23.2 0 22.222 0h.003z" />
                    </svg>
                  </a>
                </div>
              </div>
            </div>
            <div className="bg-white p-8 rounded-lg shadow-md">
              <h3 className="text-2xl font-semibold text-gray-800 mb-6">
                Send me a message
              </h3>
              {submitMessage && (
                <div className="mb-6 p-4 bg-green-100 text-green-700 rounded-lg">
                  {submitMessage}
                </div>
              )}
              <form onSubmit={handleSubmit}>
                <div className="mb-4">
                  <label
                    htmlFor="name"
                    className="block text-gray-700 font-medium mb-2"
                  >
                    Your Name
                  </label>
                  <input
                    type="text"
                    id="name"
                    name="name"
                    value={formData.name}
                    onChange={handleChange}
                    required
                    className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500"
                    placeholder="please enter your name"
                  />
                </div>
                <div className="mb-4">
                  <label
                    htmlFor="name"
                    className="block text-gray-700 font-medium mb-2"
                  >
                    Your Email
                  </label>
                  <input
                    type="email"
                    id="email"
                    name="email"
                    value={formData.email}
                    onChange={handleChange}
                    required
                    className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500"
                    placeholder="example@gmail.com"
                  />
                </div>
                <div className="mb-4">
                  <label
                    htmlFor="name"
                    className="block text-gray-700 font-medium mb-2"
                  >
                    Your Message
                  </label>
                  <textarea
                    id="message"
                    name="message"
                    value={formData.message}
                    onChange={handleChange}
                    required
                    className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500"
                    rows={5}
                    placeholder="Hello, I'd like to talk about..."
                  />
                </div>
                <button
                  type="submit"
                  disabled={isSubmitting}
                  className={`flex items-center justify-center w-full py-3 px-6 rounded-lg text-white font-medium transition-colors duration-300 ${
                    isSubmitting
                      ? "bg-gray-400 cursor-not-allowed"
                      : "bg-indigo-600 hover:bg-indigo-700"
                  }`}
                >
                  {isSubmitting ? (
                    <svg
                      className="animate-spin -ml-1 mr-3 h-5 w-5 text-white"
                      xmlns="http://www.w3.org/2000/svg"
                      fill="none"
                      viewBox="0 0 24 24"
                    >
                      <circle
                        className="opacity-25"
                        cx="12"
                        cy="12"
                        r="10"
                        stroke="currentColor"
                        strokeWidth="4"
                      ></circle>
                      <path
                        className="opacity-75"
                        fill="currentColor"
                        d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
                      ></path>
                    </svg>
                  ) : (
                    <Send size={18} className="mr-2" />
                  )}
                  {isSubmitting ? "Sending..." : "Send Message"}
                </button>
              </form>
            </div>
          </div>
        </div>
      </section>
    </>
  );
}
